  margin-top: 20px !important;
}

/* All lists */
#admin-menu,
#admin-menu .dropdown {
  line-height: 1.4em;
  list-style: none;
  margin: 0;
  padding: 0;
  z-index: 999;
}

#admin-menu .dropdown {
  position: static;
}

#admin-menu a {
  background: transparent none;
  border: none;
  color: #EEE;
  font-weight: normal;
  text-align: left;
  /* LTR */
  text-decoration: none;
}

