  z-index: 9999;
  overflow: hidden
}

#cboxOverlay {
  position: fixed;
  width: 100%;
  height: 100%
}

#cboxBottomLeft,
#cboxMiddleLeft {
  clear: left
}

#cboxContent {
  position: relative
}

#cboxLoadedContent {
  overflow: auto;
  -webkit-overflow-scrolling: touch
}

#cboxTitle {
  margin: 0
}

#cboxLoadingGraphic,
#cboxLoadingOverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%
}

#cboxClose,
#cboxNext,
#cboxPrevious,
#cboxSlideshow {
  cursor: pointer
}

.cboxPhoto {
  float: left;
  margin: auto;
  border: 0;
  display: block;
  max-width: none
}

.cboxIframe {
  width: 100%;
  height: 100%;
  display: block;
  border: 0
}

#cboxContent,
#cboxLoadedContent,
#colorbox {
  box-sizing: content-box;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box
}

#cboxOverlay {
  background: #212121
}

#cboxContent {
  margin-top: 20px
}

.cboxIframe {
  background: #f1f1f1
}

#cboxError {
  padding: 50px;
  border: 1px solid #aeb0b5
}

#cboxLoadedContent {
  border: 5px solid #212121;
  background: #f1f1f1
}

#cboxTitle {
  position: absolute;
  top: -20px;
  left: 5px;
  color: #aeb0b5
}

#cboxCurrent {
  position: absolute;
  top: -20px;
  right: 0;
  color: #aeb0b5
}

#cboxSlideshow {
  position: absolute;
  top: -20px;
  right: 116px;
  color: #f1f1f1
}

#cboxPrevious {
  position: absolute;
  top: 50%;
  left: 5px;
  margin-top: -32px;
  /* background: url(../node_modules/colorbox/controls.png) no-repeat top left; */
  width: 28px;
  height: 65px;
  text-indent: -9999px
}

#cboxPrevious:hover {
  background-position: bottom left
}

#cboxNext {
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -32px;
  /* background: url(../node_modules/colorbox/controls.png) no-repeat top right; */
  width: 28px;
  height: 65px;
  text-indent: -9999px
}

#cboxNext:hover {
  background-position: bottom right
}

#cboxLoadingOverlay {
  background: #212121
}

#cboxLoadingGraphic {
  /* background: url(../node_modules/colorbox/loading.gif) no-repeat center center */
}

#cboxClose {
  position: absolute;
  top: 5px;
  right: 0;
  display: block;
  /* background: url(../node_modules/colorbox/controls.png) no-repeat top center; */
  width: 38px;
  height: 19px;
  text-indent: -9999px
}

#cboxClose:hover {
  background-position: bottom center
}

#colorbox button {
  border: 0
}

.colorbox-hidden {
  padding: 10px
}